let html = document.querySelector("#demo")
let style = document.querySelector("#style")

let string =`/*你好，我是一名前端新人
接下来我要加样式了
我要加的的样式是*/
body{ 
    color: red;
}
`;
let String2 = " " ;
let n = 0

function step() {
    setTimeout(() => {
            //如果是回车就不照搬       
            //如果不是回车就照搬
        if (string[n] === "\n") {
            String2 += "<br>"
        } else if (string[n] === " ") {
           String2 += "&nbsp" 
        } else{ 
            String2 += string[n]
            
        }
          
        html.innerHTML = String2;
        style.innerHTML = string.substring(0, n)

        if (n < string.length -1 ) { 
            //如果n不是最后一个就继续
            n += 1 
            step();
        }



    }, 100);
}
step(); // 1=>2


setTimeout(() => {
    style.innerHTML = `
body{  
    color: red
    }    
    `;
}, 100)